	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The American Dory","date":"07 Dec 2003","slug":"/2003-12-07-the-american-dory/","tags":"Y2003"},"html":"<p>Christopher's Christmas package from Minnesota arrived yesterday. And, I admit it, I just had to take a peek... at the Finding Nemo DVD. Since the film was just released in Germany, it will be months before the DVD is available here. But it's OK. I'm an American citizen. I'm <em>allowed</em> to view Region 1 DVDs.</p>\n<p>My main suspicion was confirmed, namely that <a href=\"http://www.brainpool.de/en/stars/engelke/\">Anke Engelke</a>, the German voice of Dory, is a thousand times better than whoever did the original American Dory. But I was also disappointed. Dory's best line ('Kennen wir uns?', when she doesn't recognize Marlin the first time) is a dud in English ('Can I help you?').</p>"},"next":{"fields":{"title":"Goodbye Lenin sweeps Europe Film Prize","date":"07 Dec 2003","slug":"/2003-12-07-goodbye-lenin-sweeps-europe-film-prize/"}},"previous":{"fields":{"title":"The Ghost of Schill","date":"07 Dec 2003","slug":"/2003-12-07-the-ghost-of-schill/"}}},{"node":{"fields":{"title":"Goodbye Lenin sweeps Europe Film Prize","date":"07 Dec 2003","slug":"/2003-12-07-goodbye-lenin-sweeps-europe-film-prize/","tags":"Y2003"},"html":"<p><a title=\"Oscar for Best Foreign Film?\" href=\"http://www.reuters.co.uk/newsPackageArticle.jhtml?type=entertainmentNews&amp;storyID=416845&amp;section=news\">Goodbye Lenin sweeps Europe Film Prize</a></p>"},"next":{"fields":{"title":"Christmas Stocking","date":"06 Dec 2003","slug":"/2003-12-06-christmas-stocking/"}},"previous":{"fields":{"title":"The American Dory","date":"07 Dec 2003","slug":"/2003-12-07-the-american-dory/"}}},{"node":{"fields":{"title":"Christmas Stocking","date":"06 Dec 2003","slug":"/2003-12-06-christmas-stocking/","tags":"Y2003"},"html":"<p><img alt=\"crh_stocking.jpg\" src=\"http://www.papascott.de/wordpress/wp-content/uploads/2003/12/crh_stocking.jpg\" width=\"320\" height=\"218\" border=\"0\" /></p>\n<p>Grandma sent Christopher a Christmas stocking. Since he's not familiar with American Christmas customs, he thought he had to wear it.</p>\n<p>We'll set it out for the <a href=\"http://www.training-for-germany.de/tips99/tip11.htm\">Nikolaus</a> tonight. He's coming to our house a day late this year, and he's been informed to expect a stocking instead of a shoe. (The exact date doesn't matter, since Christopher is already a few days ahead with his Advent calendar.)</p>"},"next":{"fields":{"title":"I nearly forgot my fourth weblog anniversary","date":"05 Dec 2003","slug":"/2003-12-05-i-nearly-forgot-my-fourth-weblog-anniversary/"}},"previous":{"fields":{"title":"Goodbye Lenin sweeps Europe Film Prize","date":"07 Dec 2003","slug":"/2003-12-07-goodbye-lenin-sweeps-europe-film-prize/"}}}],"pathPrefix":"","first":false,"last":false,"index":575,"pageCount":1101}}

